
<style lang="less" scoped>
  nav {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    height: 91px;
    background-color: #fff;
    margin-top: 90px;
  }
  nav>div {
    -webkit-box-flex: 1;
    flex: 1;
    text-align: center;
    position: relative;
    height: 90px;
    line-height: 90px;
    font-size: 30px;
  }
  nav>div.active::after {
    content: '';
    display: block;
    width:  100px;
    height: 5px;
    position: absolute;
    background-color: #000;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  /* NAV 切换导航 END */
</style>

<template>
  <div>
   <div class="nav">
      <!-- 设置返回按钮 START-->
    <mt-header fixed :title="centerTitle">
      <router-link to="/mine" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <!-- 设置返回按钮 END -->
    <!-- tab 切换按钮 START -->
    <nav>
      <router-link tag="div" :to="{name:'myFriend'}">我的关注</router-link>
      <router-link tag="div" :to="{name:'myFan'}">我的粉丝</router-link>
    </nav>
   </div>
    <transition name="fade" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  export default {
    data () {
      return {
        centerTitle: '我的好友'  
      }
    },
    components: {
      headerNavBack
    },
    methods: {
      
    }
  }
</script>



